.TwoSouls {
  text-align: center;
}
.TwoSouls .app-page {
  height: 100vh;
  min-height: 600px;
  width: 100%;
  position: relative;
}
.TwoSouls .app-page .center-wrapper {
  font-size: 1.5em;
  vertical-align: middle;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.TwoSouls .app-page .center-wrapper .row {
  padding: 50px 0;
}
.TwoSouls .bg-color-gray {
  background-color: #eee;
}



